<template>
  <div class="statistiques-container">
    <!-- Indicateur de chargement global -->
    <div v-if="isLoading" class="loading-overlay">
      <i class="fas fa-spinner fa-spin"></i>
      <p>Chargement des statistiques...</p>
    </div>

    <div v-else>
      <h1 class="page-title">Tableau de bord analytique</h1>
      
      <!-- Debug Panel -->
      <div class="alert alert-info mb-3" style="background-color: #e3f2fd; border: 1px solid #2196f3; padding: 15px; border-radius: 8px;">
        <h5 style="margin: 0 0 10px 0;">Debug Statistiques</h5>
        <button @click="testApiCalls" style="background: #2196f3; color: white; border: none; padding: 8px 16px; border-radius: 4px; cursor: pointer;">Tester les appels API</button>
        <div v-if="debugInfo" style="margin-top: 10px;">
          <pre style="background: #f5f5f5; padding: 10px; border-radius: 4px; overflow-x: auto; font-size: 12px;">{{ JSON.stringify(debugInfo, null, 2) }}</pre>
        </div>
      </div>
      
      <!-- Cartes de statistiques globales -->
      <div class="stats-cards">
        <div class="stat-card">
          <div class="stat-icon"><i class="fas fa-file-alt"></i></div>
          <div class="stat-info">
            <h3>Fichiers totaux</h3>
            <p class="stat-number">{{ totalFiles }}</p>
          </div>
        </div>
        <div class="stat-card">
          <div class="stat-icon"><i class="fas fa-share-alt"></i></div>
          <div class="stat-info">
            <h3>Fichiers partagés</h3>
            <p class="stat-number">{{ sharedFiles }}</p>
          </div>
        </div>
        <div class="stat-card">
          <div class="stat-icon"><i class="fas fa-users"></i></div>
          <div class="stat-info">
            <h3>Utilisateurs actifs</h3>
            <p class="stat-number">{{ activeUsers }}</p>
          </div>
        </div>
      </div>
      
      <!-- Graphiques -->
      <div class="charts-container">
        <div class="chart-card">
          <div class="chart-header">
            <h2>Activité des documents</h2>
            <div class="time-filters">
              <button @click="setPeriod('activity', 'week')" :class="{ active: activityPeriod === 'week' }">Semaine</button>
              <button @click="setPeriod('activity', 'month')" :class="{ active: activityPeriod === 'month' }">Mois</button>
              <button @click="setPeriod('activity', 'year')" :class="{ active: activityPeriod === 'year' }">Année</button>
            </div>
          </div>
          <div class="chart-wrapper">
            <div v-if="isActivityLoading" class="chart-loading-overlay"><i class="fas fa-spinner fa-spin"></i></div>
            <div v-else-if="!activityData || !activityData.labels || activityData.labels.length === 0" class="no-data-message">
              Aucune donnée disponible
            </div>
            <Chart v-else :key="`activity-${activityPeriod}`" type="line" :data="activityData" :options="chartOptions" class="chart-content" />
          </div>
        </div>
        <div class="chart-card">
          <div class="chart-header">
            <h2>Répartition du stockage</h2>
          </div>
          <div class="chart-wrapper">
            <div v-if="!storageData || !storageData.labels || storageData.labels.length === 0" class="no-data-message">
              Aucune donnée disponible
            </div>
            <Chart v-else key="storage-chart" type="doughnut" :data="storageData" :options="doughnutOptions" class="chart-content" />
          </div>
        </div>
      </div>

      <div class="charts-container">
        <div class="chart-card full-width">
          <div class="chart-header">
            <h2>Actions des utilisateurs (6 derniers mois)</h2>
            <p class="text-muted mb-0" style="font-size: 0.85rem;">Création, Suppression, Partage, Modification, Téléchargement</p>
          </div>
          <div class="chart-wrapper">
            <div v-if="isUserActionsLoading" class="chart-loading-overlay"><i class="fas fa-spinner fa-spin"></i></div>
            <div v-else-if="!userActivityData || !userActivityData.labels || userActivityData.labels.length === 0" class="no-data-message">
              Aucune donnée disponible
            </div>
            <Chart v-else key="user-actions-chart" type="bar" :data="userActivityData" :options="barOptions" class="chart-content" />
          </div>
        </div>
      </div>

      <!-- Section Partages de Documents -->
      <div class="sharing-section">
        <div class="section-header">
          <h2><i class="fas fa-share-nodes me-2"></i>Partages de Documents</h2>
          <p class="section-subtitle">Visualisation complète des partages entre utilisateurs et services</p>
        </div>

        <!-- Statistiques rapides -->
        <div class="sharing-stats-grid">
          <div class="sharing-stat-card" v-for="(stat, index) in sharingStats" :key="index" :style="{ animationDelay: `${index * 0.1}s` }">
            <div class="stat-icon-wrapper" :style="{ background: stat.color }">
              <i :class="stat.icon"></i>
            </div>
            <div class="stat-content">
              <h4>{{ stat.label }}</h4>
              <p class="stat-value">{{ stat.value }}</p>
            </div>
          </div>
        </div>

        <!-- Graphiques de partages -->
        <div class="sharing-charts-grid">
          <!-- Graphique: Partages par utilisateur -->
          <div class="chart-card animated-card">
            <div class="chart-header">
              <h3><i class="fas fa-user-friends me-2"></i>Documents Envoyés par Utilisateur</h3>
            </div>
            <div class="chart-wrapper">
              <div v-if="isSharingLoading" class="chart-loading-overlay"><i class="fas fa-spinner fa-spin"></i></div>
              <div v-else-if="!sharingByUserData || !sharingByUserData.labels || sharingByUserData.labels.length === 0" class="no-data-message">
                Aucun partage enregistré
              </div>
              <Chart v-else key="sharing-by-user" type="bar" :data="sharingByUserData" :options="sharingBarOptions" class="chart-content" />
            </div>
          </div>

          <!-- Graphique: Partages par service -->
          <div class="chart-card animated-card">
            <div class="chart-header">
              <h3><i class="fas fa-building me-2"></i>Documents Envoyés par Service</h3>
            </div>
            <div class="chart-wrapper">
              <div v-if="isSharingLoading" class="chart-loading-overlay"><i class="fas fa-spinner fa-spin"></i></div>
              <div v-else-if="!sharingByServiceData || !sharingByServiceData.labels || sharingByServiceData.labels.length === 0" class="no-data-message">
                Aucun partage par service
              </div>
              <Chart v-else key="sharing-by-service" type="doughnut" :data="sharingByServiceData" :options="sharingDoughnutOptions" class="chart-content" />
            </div>
          </div>
        </div>

        <!-- Timeline des partages récents -->
        <div class="sharing-timeline-card animated-card">
          <div class="chart-header">
            <h3><i class="fas fa-clock me-2"></i>Documents Reçus par Utilisateur</h3>
          </div>
          <div class="timeline-wrapper" v-if="sharingTimeline && sharingTimeline.length > 0">
            <div v-for="(share, index) in sharingTimeline" :key="index" class="timeline-item" :style="{ animationDelay: `${index * 0.05}s` }">
              <div class="timeline-marker">
                <i class="fas fa-share-alt"></i>
              </div>
              <div class="timeline-content">
                <div class="timeline-header">
                  <span class="sender"><i class="fas fa-user me-1"></i>{{ share.sender }}</span>
                  <i class="fas fa-arrow-right mx-2"></i>
                  <span class="receiver"><i class="fas fa-user me-1"></i>{{ share.receiver }}</span>
                </div>
                <div class="timeline-body">
                  <span class="document-name"><i class="fas fa-file me-1"></i>{{ share.document }}</span>
                  <span class="share-date"><i class="fas fa-calendar me-1"></i>{{ share.date }}</span>
                </div>
              </div>
            </div>
          </div>
          <div v-else class="no-data-message">Aucun partage récent</div>
        </div>
      </div>

      <!-- Section Actions Récentes -->
      <div class="recent-actions-card" v-if="recentActions.length > 0">
        <div class="chart-header">
          <h2>Actions Récentes</h2>
        </div>
        <ul class="actions-list">
          <li v-for="action in recentActions" :key="action.id" class="action-item">
            <i :class="action.icon" class="action-icon"></i>
            <div class="action-details">
              <p v-html="action.description"></p>
              <span class="action-time">{{ action.time }}</span>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, onMounted, nextTick } from 'vue';
import apiClient from '@/services/api';
import Chart from 'primevue/chart';

export default defineComponent({
  name: 'StatistiquesView',
  components: {
    Chart
  },
  setup() {
    const isLoading = ref(true); // Chargement global initial
    const isActivityLoading = ref(false); // Chargement pour le graphique d'activité
    const isUserActionsLoading = ref(false); // Chargement pour le graphique des actions utilisateurs
    const isSharingLoading = ref(false); // Chargement pour les partages

    // --- Données Réactives ---
    const totalFiles = ref(0);
    const sharedFiles = ref(0);
    const activeUsers = ref(0);
    const activityData = ref<any>({ labels: [], datasets: [] });
    const storageData = ref<any>({ labels: [], datasets: [] });
    const userActivityData = ref<any>({ labels: [], datasets: [] });
    const recentActions = ref<any[]>([]);
    const debugInfo = ref<any>(null);

    // --- Données de partages ---
    const sharingStats = ref<any[]>([]);
    const sharingByUserData = ref<any>({ labels: [], datasets: [] });
    const sharingByServiceData = ref<any>({ labels: [], datasets: [] });
    const sharingTimeline = ref<any[]>([]);

    // --- Périodes de filtre ---
    const activityPeriod = ref('month');

    // --- Options des graphiques (statiques) ---
    const chartOptions = { responsive: true, maintainAspectRatio: false, plugins: { legend: { labels: { color: '#333' } } }, scales: { x: { grid: { color: 'rgba(0, 0, 0, 0.1)' }, ticks: { color: '#333' } }, y: { beginAtZero: true, grid: { color: 'rgba(0, 0, 0, 0.1)' }, ticks: { color: '#333' } } } };
    const barOptions = { ...chartOptions };
    const doughnutOptions = { responsive: true, maintainAspectRatio: false, plugins: { legend: { position: 'right', labels: { color: '#333', font: { weight: 'bold' } } } }, cutout: '60%' };
    
    // Options pour les graphiques de partages avec animations
    const sharingBarOptions = { 
      ...chartOptions, 
      animation: { duration: 1500, easing: 'easeInOutQuart' },
      plugins: { 
        legend: { display: true, labels: { color: '#333', font: { weight: 'bold' } } },
        tooltip: { backgroundColor: 'rgba(0, 0, 0, 0.8)', padding: 12, titleFont: { size: 14 }, bodyFont: { size: 13 } }
      }
    };
    const sharingDoughnutOptions = { 
      ...doughnutOptions, 
      animation: { animateRotate: true, animateScale: true, duration: 2000 },
      plugins: { 
        legend: { position: 'bottom', labels: { color: '#333', font: { weight: 'bold' }, padding: 15 } },
        tooltip: { backgroundColor: 'rgba(0, 0, 0, 0.8)', padding: 12 }
      }
    };

    // --- Fonctions de récupération granulaires ---
    const fetchActivityChartData = async () => {
        isActivityLoading.value = true;
        try {
            const response = await apiClient.get(`/statistiques/activite-documents?period=${activityPeriod.value}`);
            if (response.data && response.data.labels && response.data.datasets) {
                await nextTick();
                activityData.value = response.data;
            } else {
                console.warn("Données d'activité des documents invalides");
                activityData.value = { labels: [], datasets: [] };
            }
        } catch (error) {
            console.error("Erreur lors de la récupération des données d'activité des documents:", error);
            activityData.value = { labels: [], datasets: [] };
        } finally {
            await nextTick();
            isActivityLoading.value = false;
        }
    };
    

    const fetchUserActionsChartData = async () => {
        isUserActionsLoading.value = true;
        try {
            console.log('📊 Chargement actions utilisateurs (6 derniers mois)');
            const response = await apiClient.get('/statistiques/actions-utilisateurs');
            console.log('✅ Données actions utilisateurs reçues:', response.data);
            
            if (response.data && response.data.labels && response.data.datasets) {
                await nextTick();
                userActivityData.value = response.data;
                console.log('✅ Graphique actions utilisateurs mis à jour');
                console.log('📊 Actions suivies:', response.data.datasets.map((d: any) => d.label).join(', '));
            } else {
                console.warn('⚠️ Structure de données inattendue pour actions utilisateurs:', response.data);
                userActivityData.value = { labels: [], datasets: [] };
            }
        } catch (error: any) {
            console.error("❌ Erreur lors de la récupération des données d'actions des utilisateurs:", error);
            console.error('Détails:', error.response?.data);
            userActivityData.value = { labels: [], datasets: [] };
        } finally {
            await nextTick();
            isUserActionsLoading.value = false;
        }
    };

    // --- Fonction de récupération des partages ---
    const fetchSharingData = async () => {
        isSharingLoading.value = true;
        try {
            console.log('📊 Chargement des données de partages');
            const response = await apiClient.get('/statistiques/partages-documents');
            console.log('✅ Données de partages reçues:', response.data);
            
            if (response.data && response.data.data) {
                const data = response.data.data;
                
                // Calcul des statistiques
                const totalUsers = (data.documents_par_utilisateur || []).length;
                const totalServices = (data.documents_par_service || []).length;
                const totalReceivers = (data.documents_recus_par_utilisateur || []).length;
                
                // Statistiques rapides
                sharingStats.value = [
                    {
                        label: 'Total Partages',
                        value: data.total_partages || 0,
                        icon: 'fas fa-share-alt',
                        color: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)'
                    },
                    {
                        label: 'Utilisateurs Émetteurs',
                        value: totalUsers,
                        icon: 'fas fa-user-check',
                        color: 'linear-gradient(135deg, #f093fb 0%, #f5576c 100%)'
                    },
                    {
                        label: 'Services Impliqués',
                        value: totalServices,
                        icon: 'fas fa-building',
                        color: 'linear-gradient(135deg, #4facfe 0%, #00f2fe 100%)'
                    },
                    {
                        label: 'Utilisateurs Récepteurs',
                        value: totalReceivers,
                        icon: 'fas fa-user-friends',
                        color: 'linear-gradient(135deg, #43e97b 0%, #38f9d7 100%)'
                    }
                ];
                
                // Graphique par utilisateur (émetteurs)
                if (data.documents_par_utilisateur && Array.isArray(data.documents_par_utilisateur)) {
                    sharingByUserData.value = {
                        labels: data.documents_par_utilisateur.map((u: any) => u.user_name),
                        datasets: [{
                            label: 'Documents envoyés',
                            data: data.documents_par_utilisateur.map((u: any) => u.total_envoyes),
                            backgroundColor: 'rgba(102, 126, 234, 0.8)',
                            borderColor: 'rgba(102, 126, 234, 1)',
                            borderWidth: 2,
                            borderRadius: 8,
                            hoverBackgroundColor: 'rgba(118, 75, 162, 0.9)'
                        }]
                    };
                }
                
                // Graphique par service
                if (data.documents_par_service && Array.isArray(data.documents_par_service)) {
                    const colors = [
                        'rgba(102, 126, 234, 0.8)',
                        'rgba(245, 87, 108, 0.8)',
                        'rgba(74, 172, 254, 0.8)',
                        'rgba(67, 233, 123, 0.8)',
                        'rgba(240, 147, 251, 0.8)',
                        'rgba(255, 159, 64, 0.8)'
                    ];
                    sharingByServiceData.value = {
                        labels: data.documents_par_service.map((s: any) => s.service_nom),
                        datasets: [{
                            data: data.documents_par_service.map((s: any) => s.total_envoyes),
                            backgroundColor: colors,
                            borderColor: colors.map(c => c.replace('0.8', '1')),
                            borderWidth: 3,
                            hoverOffset: 15
                        }]
                    };
                }
                
                // Timeline des partages récents (utiliser les données disponibles)
                if (data.documents_recus_par_utilisateur && Array.isArray(data.documents_recus_par_utilisateur)) {
                    sharingTimeline.value = data.documents_recus_par_utilisateur.map((receiver: any, index: number) => {
                        // Trouver l'émetteur correspondant
                        const sender = data.documents_par_utilisateur && data.documents_par_utilisateur[0] 
                            ? data.documents_par_utilisateur[0].user_name 
                            : 'Utilisateur';
                        
                        return {
                            sender: sender,
                            receiver: receiver.user_name,
                            document: `${receiver.total_recus} document(s) reçu(s)`,
                            date: new Date().toLocaleString('fr-FR', {
                                day: '2-digit',
                                month: 'short',
                                year: 'numeric',
                                hour: '2-digit',
                                minute: '2-digit'
                            })
                        };
                    });
                }
                
                console.log('✅ Données de partages traitées avec succès');
                console.log('📊 Total partages:', data.total_partages);
                console.log('👥 Utilisateurs émetteurs:', totalUsers);
                console.log('🏢 Services:', totalServices);
            }
        } catch (error: any) {
            console.error("❌ Erreur lors de la récupération des données de partages:", error);
            console.error('Détails:', error.response?.data);
            // Initialiser avec des données vides
            sharingStats.value = [];
            sharingByUserData.value = { labels: [], datasets: [] };
            sharingByServiceData.value = { labels: [], datasets: [] };
            sharingTimeline.value = [];
        } finally {
            await nextTick();
            isSharingLoading.value = false;
        }
    };

    // --- Logique de récupération des données initiales ---
    const fetchInitialStatistics = async () => {
      isLoading.value = true;
      try {
        const [globales, repartition, actions] = await Promise.all([
          apiClient.get('/statistiques/globales'),
          apiClient.get('/statistiques/repartition-stockage'),
          apiClient.get('/statistiques/actions-recentes')
        ]);

        // Mise à jour des cartes
        if (globales.data && globales.data.data) {
          const stats = globales.data.data;
          totalFiles.value = stats.total_documents || 0;
          sharedFiles.value = stats.total_shared_documents || 0;
          activeUsers.value = stats.total_users || 0;
        }

        // Mise à jour des graphiques non-filtrables et listes
        if (repartition.data && repartition.data.data) {
            const storageStats = repartition.data.data;
            const labels = Object.keys(storageStats).map(key => key.charAt(0).toUpperCase() + key.slice(1));
            const data = Object.values(storageStats);
            storageData.value = {
                labels: labels,
                datasets: [{
                    data: data,
                    backgroundColor: ['#42A5F5', '#66BB6A', '#FFA726', '#26A69A', '#AB47BC', '#78909C'],
                    hoverBackgroundColor: ['#64B5F6', '#81C784', '#FFB74D', '#4DB6AC', '#BA68C8', '#90A4AE']
                }]
            };
        }
        recentActions.value = actions.data;

        // Lancement des chargements des graphiques filtrables et des partages
        await Promise.all([fetchActivityChartData(), fetchUserActionsChartData(), fetchSharingData()]);

      } catch (error: any) {
        console.error("Erreur lors de la récupération des statistiques initiales:", error.response?.data?.message || error.message);
      } finally {
        isLoading.value = false;
      }
    };

    // --- Changement de période ---
    const setPeriod = (chart: 'activity', period: 'week' | 'month' | 'year') => {
      if (chart === 'activity') {
        if (activityPeriod.value === period) return; // Ne rien faire si la période est la même
        activityPeriod.value = period;
        fetchActivityChartData();
      }
    };

    // --- Fonction de debug ---
    const testApiCalls = async () => {
      debugInfo.value = { testing: true, results: {} };
      
      const endpoints = [
        '/statistiques/globales',
        '/statistiques/repartition-stockage', 
        '/statistiques/actions-recentes',
        '/statistiques/activite-documents?period=month',
        '/statistiques/actions-utilisateurs'
      ];

      for (const endpoint of endpoints) {
        try {
          console.log(`Testing: ${endpoint}`);
          const response = await apiClient.get(endpoint);
          debugInfo.value.results[endpoint] = {
            status: response.status,
            data: response.data,
            success: true
          };
        } catch (error: any) {
          console.error(`Error on ${endpoint}:`, error);
          debugInfo.value.results[endpoint] = {
            status: error.response?.status || 'Network Error',
            error: error.message,
            response: error.response?.data,
            success: false
          };
        }
      }
      
      debugInfo.value.testing = false;
    };

    // --- Cycle de vie ---
    onMounted(() => {
      // Petit délai pour s'assurer que le DOM est prêt
      setTimeout(() => {
        fetchInitialStatistics();
      }, 100);
    });

    return {
      isLoading,
      isActivityLoading,
      isUserActionsLoading,
      isSharingLoading,
      totalFiles,
      sharedFiles,
      activeUsers,
      activityData,
      storageData,
      userActivityData,
      recentActions,
      sharingStats,
      sharingByUserData,
      sharingByServiceData,
      sharingTimeline,
      chartOptions,
      barOptions,
      doughnutOptions,
      sharingBarOptions,
      sharingDoughnutOptions,
      activityPeriod,
      setPeriod,
      debugInfo,
      testApiCalls,
    };
  }
});
</script>

